/* Games Component Styles */

/* GameSelector Styles */
.game-selector-container {
  max-width: 56rem;
  margin: 0 auto;
  padding: 3rem 1rem;
}

.game-selector-header {
  text-align: center;
  margin-bottom: 3rem;
}

.game-selector-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.1));
}

.game-selector-subtitle {
  font-size: 1.25rem;
}

.game-selector-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
  gap: 1.5rem;
  justify-items: center;
}

.game-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 36rem;
  padding: 1rem;
  margin-bottom: 2.5rem;
}

/* GameCard Styles */
.game-card {
  position: relative;
  width: 12rem;
  height: 12rem;
  border-radius: 1rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transform: scale(1);
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.game-card:hover {
  transform: scale(1.05);
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

.game-card:focus {
  outline: none;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.5);
}

.game-card:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.game-card:disabled:hover {
  transform: scale(1);
}

.game-card-overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.3s ease;
}

.game-card:hover .game-card-overlay {
  background-color: rgba(0, 0, 0, 0.2);
}

.game-card-content {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  color: white;
}

.game-card-icon {
  font-size: 3rem;
  margin-bottom: 0.75rem;
  transform: scale(1);
  transition: transform 0.3s ease;
}

.game-card:hover .game-card-icon {
  transform: scale(1.1);
}

.game-card-title {
  font-size: 1.25rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 0.25rem;
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.1));
}

.game-card-description {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
}

.game-card-accent-top {
  position: absolute;
  top: 0;
  right: 0;
  width: 4rem;
  height: 4rem;
}

.game-card-accent-triangle {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-top: 3rem solid rgba(255, 255, 255, 0.2);
  border-left: 3rem solid transparent;
}

.game-card-accent-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.25rem;
  background: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.3), transparent);
}

/* GameOverlay Styles */
.game-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
  padding: 1rem;
  overflow-y: auto;
}

.game-overlay-bg {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(4px);
}

.game-overlay-card {
  position: relative;
  background: linear-gradient(135deg, #1f2937 0%, #374151 100%);
  padding: 2rem;
  border-radius: 1rem;
  border: 2px solid #4b5563;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  transform: scale(1);
  transition: all 0.3s ease;
  max-width: 32rem;
  width: 100%;
  margin: auto;
}

.game-overlay-card:hover {
  transform: scale(1.05);
}

.game-overlay-glow {
  position: absolute;
  inset: -0.25rem;
  border-radius: 1rem;
  opacity: 0.2;
  filter: blur(40px);
}

.game-overlay-content {
  position: relative;
  z-index: 10;
}

.game-overlay-title {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1rem;
  color: white;
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.1));
}

.game-overlay-score {
  text-align: center;
  margin-bottom: 1rem;
}

.game-overlay-score-label {
  font-size: 1.125rem;
  color: #d1d5db;
  margin-bottom: 0.25rem;
}

.game-overlay-score-value {
  font-size: 2.25rem;
  font-weight: bold;
  color: #fbbf24;
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.1));
}

.game-overlay-level {
  text-align: center;
  margin-bottom: 1rem;
}

.game-overlay-level-label {
  font-size: 1.125rem;
  color: #d1d5db;
  margin-bottom: 0.25rem;
}

.game-overlay-level-value {
  font-size: 1.875rem;
  font-weight: bold;
  color: #34d399;
}

.game-overlay-message {
  text-align: center;
  color: #d1d5db;
  margin-bottom: 1.5rem;
  font-size: 1.125rem;
}

.game-overlay-button {
  width: 100%;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: bold;
  font-size: 1.125rem;
  transition: all 0.2s ease;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  margin-top: 1rem;
  color: white;
  border: none;
  cursor: pointer;
}

.game-overlay-button:hover {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
  transform: scale(1.05);
}

.game-overlay-blink {
  text-align: center;
  font-size: 1.125rem;
  font-weight: 600;
  transition: opacity 0.3s ease;
}

/* HUD Styles */
.hud-top {
  position: absolute;
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  top: 0;
  width: 100%;
  padding: 0.5rem;
  z-index: 40;
}

.hud-lap-counter {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  height: 100%;
  padding: 0.25rem 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 0.5rem;
  font-weight: 600;
  text-align: center;
}

.hud-lap-icon {
  height: 1.5rem;
}

.hud-lap-text {
  font-size: 1rem;
}

.hud-speed {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.25rem 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 0.5rem;
  font-weight: 600;
}

.hud-speedometer {
  width: 3rem;
  height: 3rem;
  position: relative;
}

.hud-speedometer-gauge {
  width: 100%;
  height: 100%;
  fill: white;
  opacity: 0.9;
}

.hud-speedometer-needle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 1.25rem;
  background-color: #ef4444;
  transform-origin: center bottom;
  transition: transform 0.1s ease-out;
}

.hud-speed-text {
  font-size: 0.875rem;
}

.hud-score {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.25rem 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 0.5rem;
  font-weight: 600;
}

.hud-score-label {
  font-size: 0.875rem;
  color: #fbbf24;
  text-transform: uppercase;
}

.hud-score-value {
  font-size: 1.125rem;
}

.hud-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.5rem;
  z-index: 40;
}

.hud-bottom-center {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}

.hud-time {
  padding: 0.25rem 0.75rem;
  color: rgba(255, 255, 255, 0.9);
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 0.5rem;
  font-weight: 600;
  text-align: center;
}

.hud-time-label {
  font-size: 0.75rem;
  color: #fbbf24;
  text-transform: uppercase;
}

.hud-time-value {
  font-size: 1.125rem;
}

.hud-controls {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding: 0.5rem;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0.5rem;
  color: rgba(255, 255, 255, 0.9);
  z-index: 40;
  font-size: 0.75rem;
}

.hud-control-item {
  display: flex;
  gap: 0.5rem;
}

.hud-control-key {
  font-weight: 600;
  color: #fbbf24;
}

/* BillboardStats Styles */
.billboard-stats {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 0.75rem;
  border-radius: 0.5rem;
  min-width: 10rem;
  z-index: 40;
}

.billboard-title {
  color: #fbbf24;
  font-weight: bold;
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
}

.billboard-stat {
  display: flex;
  justify-content: space-between;
  color: white;
  font-size: 0.75rem;
  margin-bottom: 0.25rem;
}

.billboard-stat-label {
  color: #9ca3af;
}

.billboard-stat-value {
  font-weight: 600;
}

/* GameCanvas Styles */
.game-canvas-container {
  position: relative;
  width: 100%;
  max-width: 100%;
  background-color: #1f2937;
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

.game-canvas {
  display: block;
  width: 100%;
  height: auto;
  image-rendering: optimizeSpeed;
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: optimize-contrast;
  image-rendering: pixelated;
}

/* GameHeader Styles */
.game-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem;
  background: linear-gradient(135deg, #4b5563 0%, #374151 100%);
  color: white;
  border-radius: 0.5rem 0.5rem 0 0;
}

.game-header-title {
  font-size: 1.125rem;
  font-weight: bold;
}

.game-header-score {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.game-header-score-label {
  font-size: 0.875rem;
  color: #9ca3af;
}

.game-header-score-value {
  font-size: 1.125rem;
  font-weight: bold;
  color: #fbbf24;
}

/* GameControls Styles */
.game-controls {
  display: flex;
  gap: 0.5rem;
  padding: 0.75rem;
  background-color: #374151;
  border-radius: 0 0 0.5rem 0.5rem;
}

.game-controls-button {
  flex: 1;
  padding: 0.5rem 1rem;
  background-color: #4b5563;
  color: white;
  border: none;
  border-radius: 0.375rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.game-controls-button:hover {
  background-color: #6b7280;
}

.game-controls-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .game-overlay-card {
    padding: 1.5rem;
  }

  .game-overlay-title {
    font-size: 1.5rem;
  }

  .game-selector-title {
    font-size: 2.5rem;
  }

  .game-selector-grid {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
  }

  .game-card {
    width: 10rem;
    height: 10rem;
  }
}

@media (max-width: 640px) {
  .game-selector-grid {
    grid-template-columns: 1fr;
  }

  .hud-controls {
    font-size: 0.625rem;
    padding: 0.375rem;
  }

  .hud-speedometer {
    width: 2.5rem;
    height: 2.5rem;
  }
}
