.ui.container.main {
  position: relative;
  height: calc(100% - 80px);
  max-height: calc(100% - 80px);
  overflow: hidden;
}
.ui.container.main .ui.grid {
  height: 100%;
  max-height: 100%;
  overflow: hidden;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}
.ui.container.main .ui.grid .column {
  display: flex !important;
  justify-content: center;
  flex-direction: column;
  align-content: center;
  align-items: center;
}
.seven.wide.column {
  z-index: 1;
  height: 100%;
  padding: 10px;
}
.nine.wide.column {
  z-index: 0;
  height: 100%;
  padding: 10px;
}
.left-side-wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 5fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    'avatar dialog'
    'actions actions';
  height: 472px;
}
div#dialog-box-container {
  width: 100%;
  height: 320px;
  grid-area: dialog;
  padding-top: 40px;
}
div#dialog-box {
  position: relative;
  display: flex;
  align-items: center;
  background-color: var(--transparent-white);
  width: calc(100% - 8px);
  height: auto;
  min-height: 100px;
  max-height: 100%;
  padding: 20px 30px 24px 30px;
  margin: 4px;
  filter: drop-shadow(-2px 6px 6px var(--world-shadow-color));
}
.night div#dialog-box {
  background-color: var(--transparent-black);
  border-radius: 4px;
}
div#dialog-box::before {
  content: '';
  width: 0px;
  height: 0px;
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 10px solid var(--transparent-white);
  border-top: 10px solid transparent;
  border-bottom: 10px solid var(--transparent-white);
  left: -24px;
  top: 60px;
}
.night div#dialog-box::before {
  border-right: 10px solid var(--transparent-black);
  border-bottom: 10px solid var(--transparent-black);
  left: -20px;
}
div#dialog-box p {
  font-family: 'VG';
  font-size: 12px;
  line-height: 22px;
  display: inline;
}
div#actions-wrapper {
  display: grid;
  grid-template-columns: 2fr 5fr;
  width: 100%;
  filter: drop-shadow(-2px 6px 6px var(--world-shadow-color));
  grid-area: actions;
}
div#action-suggestion {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  background-color: var(--transparent-white);
  z-index: 1;
}
.night div#action-suggestion {
  background-color: var(--transparent-black);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
div#action-suggestion p {
  color: #333;
  font-size: 10px;
  text-align: left;
  text-transform: uppercase;
}
.night div#action-suggestion p {
  color: #bbb;
}
div#actions {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background-color: var(--transparent-white);
  overflow: hidden;
}
.night div#actions {
  background-color: var(--transparent-black);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
button.visitor-action {
  position: relative;
  width: 100%;
  height: 36px;
  text-align: left;
  font-family: 'VG';
  font-size: 12px;
  padding-left: 20px;
  background-color: transparent;
  border: none;
}
button.visitor-action:focus {
  background-color: #c0e0e0;
  color: #000;
  border: none;
  outline: 0;
}
button.visitor-action:hover {
  background-color: #a3c7c7;
  color: #000;
}
button.visitor-action::before {
  content: '-';
  position: absolute;
  left: 0px;
  top: 1px;
  height: 30px;
  color: #000;
  font-size: 16px;
  line-height: 34px;
}
.night button.visitor-action::before {
  color: #bbb;
}
button.visitor-action:hover::before {
  content: '>';
  position: absolute;
  left: 0px;
  top: 1px;
  height: 16px;
  color: #000;
  font-size: 16px;
  line-height: 34px;
  opacity: 1;
  animation: blink 0.5s linear 0.1s infinite;
}
.ui.button,
.ui.buttons .button,
.ui.buttons .or {
  font-size: 0.8rem;
  margin-top: 10px;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
div#dialog-box,
div#actions-wrapper {
  border-style: solid;
  border-width: 4px;
  border-image-slice: 2;
  border-image-width: 2;
  border-image-repeat: stretch;
  border-image-outset: 1;
  background-clip: padding-box;
}
.night div#dialog-box,
.night div#actions-wrapper {
  border: none;
}
.right-side-wrapper {
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  position: relative;
  display: flex;
  justify-content: center;
  margin: 10px;
}

/* When browser is maximized, ensure parent column has highest z-index */
.nine.wide.column:has(.browser-window.maximized) {
  z-index: 100000 !important;
}

/* Also remove stacking context from wrapper when browser is maximized */
.nine.wide.column:has(.browser-window.maximized) .right-side-wrapper {
  position: static !important;
}
.right-side-wrapper h1,
.right-side-wrapper h2,
.right-side-wrapper h3 {
  margin: 15px 0;
  overflow-wrap: anywhere;
}

@media screen and (max-width: 992px) {
  .ui.container.main .ui.grid .seven.wide.column {
    align-items: baseline;
  }
}
@media screen and (max-width: 768px) {
  .ui.container.main .ui.grid .seven.wide.column {
    margin-top: 0px !important;
    padding-top: 25svh;
  }
  div#avatar-container,
  div#dialog-box-container {
    height: 250px;
  }
  div#loader {
    height: 100%;
    align-items: initial;
    padding-top: 240px;
    font-size: 52px;
  }
  div#dialog-box {
    align-items: start;
    padding: 12px;
  }
  .dialog-box-wrap {
    max-height: 100%;
  }
  div#actions-wrapper {
    grid-template-columns: 1fr;
    height: 160px;
    margin-top: 10px;
  }
}
@media screen and (max-width: 540px) {
  .ui.container.main {
    height: 100svh;
    max-height: 100svh;
  }
  .ui.container.main .ui.grid .seven.wide.column {
    margin-top: 0px !important;
    height: 100svh;
    align-items: center;
    padding-top: 20svh;
  }
  .left-side-wrapper {
    transform: translateY(-20%);
  }
  .ui.container.main .ui.grid .nine.wide.column {
    position: fixed;
    bottom: calc(-100% + 80px);
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 9999;
    transition: bottom 0.3s ease-in-out;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding-top: 60px;
  }
  .ui.container.main .ui.grid .nine.wide.column::before {
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  .ui.container.main .ui.grid .nine.wide.column.browser-open {
    bottom: -34px;
  }
  /* Dark overlay when browser is open */
  .ui.container.main .ui.grid .nine.wide.column.browser-open::before {
    opacity: 1;
  }
  div#avatar-container,
  div#dialog-box-container {
    height: 280px;
  }
  div#dialog-box-container {
    display: flex;
  }
  div#dialog-box p {
    font-size: 14px !important;
    line-height: 20px !important;
  }
  div#actions-wrapper {
    margin-bottom: 14px;
  }
  .night div#actions {
    border-radius: 8px;
  }
  div#actions > * {
    padding-left: 30px;
  }
  button.visitor-action::before,
  button.visitor-action:hover::before {
    left: 10px;
  }
  div#action-suggestion {
    display: none;
  }
  div#action-suggestion p {
    font-size: 8px !important;
  }
  button.visitor-action {
    font-size: 10px !important;
  }
  .right-side-wrapper {
    width: 100%;
    height: 100%;
    margin: 0;
  }
  h1 {
    font-size: 30px !important;
  }
  h2 {
    font-size: 24px !important;
  }
  h3 {
    font-size: 18px !important;
  }
  p {
    font-size: 10px !important;
  }
}
@media screen and (max-width: 400px) {
  h1 {
    font-size: 25px !important;
  }
  h2 {
    font-size: 16px !important;
  }
  h3 {
    font-size: 12px !important;
  }
  div#dialog-box p {
    font-size: 10px !important;
    line-height: 16px !important;
  }
}
@media screen and (min-width: 992px) and (max-width: 1198px) {
  div#dialog-box p {
    font-size: 15px !important;
    line-height: 26px !important;
  }
  button.visitor-action {
    font-size: 10px !important;
  }
  .right-side-wrapper {
    width: 100%;
  }
}
@media screen and (min-width: 768px) and (max-width: 992px) {
  .ui.container.main .ui.grid .seven.wide.column {
    margin-top: 0px !important;
  }
  .left-side-wrapper {
    height: 412px;
  }
  div#avatar-container {
    width: 100px;
  }
  div#avatar-container,
  div#dialog-box-container {
    height: 250px;
  }
  img#avatar {
    width: 100px;
    margin-top: 40px;
  }
  div#dialog-box p {
    font-size: 12px !important;
    line-height: 16px !important;
  }
  button.visitor-action {
    font-size: 10px !important;
    padding-left: 20px;
  }
  button.visitor-action::before,
  button.visitor-action:hover::before {
    font-size: 10px;
    left: 6px;
  }
  .right-side-wrapper {
    width: 100%;
  }
}
@media screen and (min-width: 1200px) and (max-height: 768px) {
  .ui.grid > .column:not(.row) {
    padding: 1rem 0 0 0;
  }
  .seven.wide.column {
    z-index: 1;
    margin: 70px 0 20px;
  }
}
